//
(function(doc, win){
    //1.HTML
    let html = doc.getElementsByTagName("html")[0],
    //2.=
    reEvt = "orientationchange" in win ? "orientationchange" : "resize",///
    //3.
    reFontSize = function() {
      var clientW = doc.documentElement.clientWidth || doc.body.clientWidth;//
      if(!clientW) {return;}
      /// = 16/1200,/ = 1rem/clientW
      // 1rem = 16*clientW/1200
      //  = 1rem*1200/16
      html.style.fontSize = 16*clientW/1200 + "px";
      // window.console.log(html.style.fontSize)
    }
    //5.(/)
    win.addEventListener(reEvt, reFontSize);
    //6.DOMContentLoaded->dom,onloaddom/css/js
    doc.addEventListener("DOMContentLoaded", reFontSize);
})(document, window);

//VUE
import Vue from 'vue'
import App from './App.vue'
import router from './router/router.js';

Vue.config.productionTip = false

new Vue({

  render: h => h(App),//,#app

  router

}).$mount('#app')//#app
