<template>
    <div class='orders'>
        <!--  -->
        <myNav></myNav>

        <!--  -->
        <div class="order" v-for="(order,index) in myOrders" :key='index'>
            <!--  -->
            <img :src='order.goodMsg.img'>
            <!--  -->
            <div class="orderMsg">
                <div>{{ order.goodMsg.name }}</div>
                <div> {{ order.goodMsg.price }}</div>
            </div>
            <div class="orderMsg">
                <div></div>
                <div>{{ order.goodMsg.num+" " }}</div>
            </div>
            <!--  -->
            <div class="orderBtn">
                <div>
                    <a style="cursor:default;">{{ states[order.state].msg0}}</a>
                </div>
                <div :class='orderCss[states[order.state].msg1[1]]' @click="stateChange(order.state,index)">
                    <a>{{ states[order.state].msg1[0]}}</a>
                </div>
                <div :class='orderCss[states[order.state].msg2[1]]' @click="cancelOrder(index)"
                    v-show="states[order.state].msg2[1]==3?false:true">
                    <a >{{ states[order.state].msg2[0]}}</a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import myNav from '../components/myNav.vue'
    export default {
        data:function(){
            return{
                states:[
                    {msg0:'', msg1:['',1], msg2:['',0],},//0
                    {msg0:'', msg1:['',2], msg2:['',3]},//1
                    {msg0:'', msg1:['',3], msg2:['',0]},//2
                    {msg0:'', msg1:['',3], msg2:['',3]},//3
                    {msg0:'', msg1:['',3], msg2:['',3]},//4
                    {msg0:'', msg1:['',1], msg2:['',3]},//5
                ],
                orderCss:['btnRed','btnPay','btnBack','btnNone'], 
                myOrders:[
                    // /// - 
                    // { 
                    //     state:0, 
                    //     goodMsg:{
                    //         img:require('../assets/image/items/adidas/01.jpg'),
                    //         name:'adidas 365',
                    //         price:'199',
                    //         num:'1',
                    //     }, },
                ],
            }
        },
        props:['DBfromApp'],//AppDB
        methods:{
            stateChange(stateNum,index){// = 
                switch(stateNum){
                    //
                    case 0:{
                        if(window.confirm("")){// window.console.log('')
                            this.DBfromApp.getOrdersDB().changeState(localStorage.club1023User,index,stateNum);
                            // window.console.log('')
                            this.upDataOrders();
                        }else{
                            return false;
                        }
                        break;
                    }
                    //
                    case 1:{
                        if(window.confirm("")){// window.console.log('')
                            this.DBfromApp.getOrdersDB().changeState(localStorage.club1023User,index,stateNum);
                            // window.console.log('')
                            this.upDataOrders();
                        }else{
                            return false;
                        }
                        break;
                    }
                }
            },
            cancelOrder(index){//
                if(window.confirm("")){
                    // window.console.log('')
                    this.DBfromApp.getOrdersDB().delOrder(localStorage.club1023User,index);
                    // window.console.log('')
                    this.upDataOrders();
                }
            },
            upDataOrders(){
                // 
                if(localStorage.club1023User=="") return window.console.log("");//
                let dbData = this.DBfromApp.getOrdersDB().queryOrders(localStorage.club1023User);
                //[['',[///]],....]
                // window.console.log(' ')
                // window.console.log(dbData)
                if(dbData.length==0){
                    this.myOrders = Object.assign([]);
                    return true;
                }
                let temp = [];
                for(let i=0;i<dbData.length;i++){
                    temp[i] = { 
                        state:dbData[i][1][4], 
                        goodMsg:{
                            img:dbData[i][1][1],
                            name:dbData[i][0],
                            price:dbData[i][1][0],
                            num:dbData[i][1][2]
                        }
                    }
                    // window.console.log(dbData[0])
                    // window.console.log(dbData.length)
                }
                this.myOrders = Object.assign([],this.myOrders, temp);
                
                // window.console.log(' ')
                // window.console.log(this.myOrders)
            }
        },
        components:{
            myNav,
        },
        created(){
            // window.console.log('orders')
            this.upDataOrders();
        },
    }
</script>

<style scoped>
    @import '../assets/css/orders/orders.css';
</style>
