<template>
    <!--  -->
    <div class="loginBtns">
        <!--  -->
        <div class="loginBtns_05" v-show='view0'>
            <router-link to='login' class='btn' v-show="viewBtns"></router-link>
            <a class='btn' @click="showBtns()"></a>
        </div>
        <!--  -->
        <div class="loginBtns_05" v-show='view1'>
            <a class="btn" @click='toOrderVue()' v-show="viewBtns"></a>
            <a class='btn' @click='showCar()' v-show="viewBtns"></a>
            <a to='login' class='btn' @click="logout()" v-show="viewBtns"></a>
            <a class='btn' @click="showBtns()">{{ viewBtns?'':'' }}</a>
        </div>

        <!--  -->
        <myCar v-if="carView" :DBfromApp='DBfromApp'></myCar>
    </div>
</template>

<script>
    import myCar from './myCar.vue';

    export default {
        data:function(){
            return{
                userName:'',
                view0:true,//
                view1:false,
                carView:false,
                viewBtns:true,//
            }
        },
        props:['DBfromApp'],//AppDB
        watch:{
            userName:function(){
                if(localStorage.club1023User=="") this.viewsOn();
            }
        },
        methods:{
            showBtns(){
                this.viewBtns = !this.viewBtns;
            },
            toOrderVue(){
                if(this.userName!=""){
                    this.$router.push({ name:'orders',params:{ name:this.userName } });
                }
            },
            viewsOn:function(){// 
                this.view0 = !this.view0;
                this.view1 = !this.view1;
            },
            logout:function(){//
                localStorage.club1023User="";
                this.userName = localStorage.club1023User;
                window.console.log('::  '+localStorage.club1023User);
            },
            showCar:function(){//
                this.carView = !this.carView
                // window.console.log(this.carView)
            },
        },
        components:{
            myCar,
        },
        created(){
            this.userName = localStorage.club1023User;//
            if(this.userName != ""){ this.viewsOn() }
            window.console.log('btns - :  '+localStorage.club1023User);
        }
    }
</script>

<style scoped>
    @import '../assets/css/index/btns_5.css';
</style>
