<template>
  <div class='myCar'>
      <div class="car">
          <!--  -->
          <div class="close" >
              <img :src="require('../assets/image/detail/close.jpg')" @click='closeCar()'>
          </div>
          <!--  -->
          <div class="orders" v-for='(goods,goodKey) in carData' :key='goodKey'>
              <input class='orderBox' type="checkbox" :value='[goodKey,goods]' v-model='ordersData'>
              <img :src='goods[1]'>
              <div class="msg">{{ goodKey+' - $'+goods[0]+'.00' }}</div>
              <div class="btns" @click='addCarData1(goods[3],goodKey)'>+</div>
              <div class="num">{{goods[2]}}</div>
              <div class="btns" @click='delCarData1(goodKey)'>-</div>
          </div>
          <!--  -->
          <div class="carBtn">
              <div class="btns">-</div>
              <div class="num">1</div>
              <div class="btns">+</div>
              <a class="carBtns" @click="sendOrder()" ></a>
          </div>
      </div>
  </div>
</template>

<script>
    export default {
      data:function(){
        return {
            userName:localStorage.club1023User,
            ordersData:[],// - [['',[///],....]
            carData:{},
        }
      },
      props:['DBfromApp'],//AppDB
      methods:{
          sendOrder(){
            //bug : router-link /-
            //bug : 
            //0.
            this.DBfromApp.getOrdersDB().addOrder(localStorage.club1023User,this.ordersData);
            //1. - 
            for(let i=0;i<this.ordersData.length;i++){
              this.DBfromApp.getCarDB().delOrderItem(localStorage.club1023User,this.ordersData[i][0]);
            }
            this.updataCar();//

            //2.
            this.$router.push({ name:'orders',params:{ name:this.userName } });
          },
          closeCar:function(){//
              this.$parent.showCar();
          },

          addCarData1:function(name,good){//
            if(this.checkUser){
                let arr = this.DBfromApp.getItem(name,good);
                arr[2] = name;//
                this.DBfromApp.getCarDB().addItem(localStorage.club1023User,good,arr);//
                let temp = this.DBfromApp.getCarDB().queryUserCar([localStorage.club1023User]);//
                this.carData = Object.assign({},this.carData, temp);
            }
          },

          delCarData1:function(good){//
            if(this.checkUser){
                this.DBfromApp.getCarDB().delItem(localStorage.club1023User,good);//
                let temp = this.DBfromApp.getCarDB().queryUserCar([localStorage.club1023User]);//
                if(!temp.hasOwnProperty(good)){//
                  this.carData = {};
                  this.carData = Object.assign({}, temp);//
                }else{
                  this.carData = Object.assign({}, this.carData, temp);//
                }
            }
          },
          checkUser(){
            if(localStorage.club1023User == ""){//
                window.console.log('');
                return false;
            }
            return true;
          },
          updataCar(){
            let temp = this.DBfromApp.getCarDB().queryUserCar([localStorage.club1023User]);//

            if(JSON.stringify(temp) == "{}"){//
              // window.console.log('test1')
              this.carData = {};
            }else{
              this.carData = Object.assign({}, temp);//
            }
          },
        },
        created(){
          //
          // window.console.log('')
          this.updataCar();
        },
    }
</script>

<style scoped>
  @import '../assets/css/App/myCar.css';
</style>
