
function orders(){

    let ordersDB = {};//{user:...}
    ordersDB['admin']=[];

    return {
        changeState(userName,index,stateNum){//
            if(userName=="")return false;
            switch(stateNum){
                case 0:{ordersDB[userName][index][1][4]=3;break;}
                // case 1:{ordersDB[userName][index][1][4]=3;break;}
            }
        },
        delOrder(userName,index){//-
            if(userName=="")return false;
            ordersDB[userName].splice(index,1); //arr.splice(2,1,"")""2,1
        },
        addOrder(userName,orders){//orders = [['',[////],....]
            for(let i=0;i<orders.length;i++){
                // window.console.log(orders[i]);
                orders[i][1][4]=0;
                ordersDB[userName].push(orders[i]);//
            }
        },
        queryOrders(userName){
            return ordersDB[userName];//
        },
        getOrders(){//
            return ordersDB;
        },
    }
}
export default orders;