<template>
    <div @wheel="addItem">
        <myNav></myNav>
        <div class="items">
            <div class="head">{{ name }}  :</div>
            <a class="itemCon" v-for='(arr,arrKey,index) in main' :key='index'>
                <div class="itemOne" >
                    <router-link to='/'>
                        <img :src='arr[1]' @click.prevent = routesAdd(name,arrKey)>
                    </router-link>
                    <div class="label">
                        <div>{{ arrKey }}</div>
                        <div>${{ arr[0] }}.00 </div>
                    </div>
                </div>
            </a>
        </div>
    </div>
</template>

<script>
    import myNav from '../components/myNav.vue'
    export default {
        data:function(){
            return{
                name:"",
                goods : {},//
                main:[],//:
                count:0,//:
            }
        },
        props:['DBfromApp'],//AppDB
        methods:{
            addItem:function(){// 
                // maincurrent
                // 1. main
                this.main = {};
                // 2. main
                this.count += 12;
                let countTemp = 0;
                for(let key in this.goods){
                    this.main[key] = this.goods[key];
                    countTemp++;
                    if(countTemp == this.count){
                        break;
                    }
                }
            },
            // 
            routesAdd:function(name,good){
                this.$router.push(
                    {path:'/items/'+name+'/'+good}
                );
            },
        },
        components:{
            myNav,
        },
        created:function(){
            this.name = this.$route.params.name// 1.
            this.goods = this.DBfromApp.getItems(this.name);//2.
            this.addItem();//
        },
    }
</script>

<style scoped>
    @import '../assets/css/items/items.css';
</style>
