<template>
    <div class='good'>

        <!--  -->
        <div class="goodImg block" :style="imgSize">
            <img :src='images[1]'>
        </div>

        <!--  -->
        <div class="goodMsg block" :style="imgSizeRight">
            <img src='../assets/image/detail/timg.jpg'>
        </div>
        <!--  -->
        <div class="goodMsg block mobileGMS">
            <img src='../assets/image/detail/timg.jpg'>
        </div>

        <!--  -->
        <div class="goodBtns">
            <div @click='widthChange' class='divbtn no'>{{ imgMsg }}</div>
            <router-link :to="{ name:'items', params:{ name:name }}" class='divbtn'></router-link>
            <div class='divbtn' v-show="btnView" @click="addGood()"></div>
            <div class='divbtn' v-show="btnView" @click="showCar()"></div>
            <router-link to='/login' class='divbtn' v-show="!btnView"></router-link>
            <!-- <div class='divbtn' v-show="!btnView" @click="addGood()"></div> -->
        </div>

        <!--  -->
        <myCar v-show="carView" 
            :DBfromApp='DBfromApp' 
            ref='carCom' 
        ></myCar>
    </div>
</template>

<script>
    import myCar from '../components/myCar.vue';
    export default {
        data:function(){
            return{

                // 
                name:"",
                good:"",
                images:{},

                // 
                imgMsg:'',
                imgNum:100,
                btnView:false,
                carView:false,//
            }
        },
        props:['DBfromApp'],//AppDB
        computed:{
            // 
            imgSize:function(){return{
                    width:(this.imgNum)+'%'
                }
            },
            imgSizeRight:function(){
                return{
                    width:(100-this.imgNum)+'%'
                }
            },
        },
        methods:{
            // 
            widthChange:function(){
                if(this.imgNum==70){
                    this.imgNum=100;
                    this.imgMsg='';
                    return;
                }
                this.imgNum = 70;
                this.imgMsg='';
            },
            showCar:function(){//
                this.carView = !this.carView;
            },
            addGood:function(){//
                //
                this.$refs.carCom.addCarData1(this.$route.params.name,this.$route.params.good);
                this.showCar();
            },
        },
        components:{
            myCar:myCar,
        },
        created(){
            if(localStorage.club1023User != ""){ this.btnView = !this.btnView }
            //
            this.name = this.$route.params.name;//
            this.good = this.$route.params.good;//
            this.images = this.DBfromApp.getItem(this.name,this.good);//[price,src]
        },
    }
</script>

<style scoped>
    @import '../assets/css/good/good.css';
</style>
