

function userCar(){

    let carDB = {};//{user:{...}}
    carDB['admin']={};
    return {
        getCarDB(){//
            return carDB;
        },
        queryUserCar:function(userName){//-
            return carDB[userName];
        },
        addItem:function(userName,goodName,arr){//////
            // window.console.log(carDB[userName])
            // window.console.log(carDB)
            if(carDB[userName].hasOwnProperty(goodName)){//,+1 
                // window.console.log('4 -  +1 ')
                // window.console.log(carDB)
                carDB[userName][goodName][2]++;
                return true;
            }
            
            // window.console.log('4 -  =1 ')
            // window.console.log(carDB)
            carDB[userName][goodName] = [];
            carDB[userName][goodName][0] = arr[0];//
            carDB[userName][goodName][1] = arr[1];//
            carDB[userName][goodName][2] = 1;
            carDB[userName][goodName][3] = arr[2]
            return true;
        },
        delItem:function(userName,goodName){//
            if(carDB[userName][goodName][2]==1){//1
                delete carDB[userName][goodName];
                return true;
            }
            //-1
            carDB[userName][goodName][2]--;
            return true;
        },
        delOrderItem:function(userName,goodName){///
            delete carDB[userName][goodName];
        },
    }
}
export default userCar;