import goodsDBjs from './goodsDB.js';
import cars from './userCar.js';
import ordersJS from './orders.js';


// 1./
var myDB = function(){

    var userDB = [['admin','0']];// 
    var goodsDB = goodsDBjs;
    var userCar = cars(userDB);//
    var orders = ordersJS();//
    
    return {
        //**********************************************************************
        getUserDB(){//
            return userDB;
        },
        getGoodsDB(){//
            return goodsDB;
        },
        getCarDB(){//
            return userCar;
        },
        getOrdersDB(){//
            return orders;
        },
        

        //**********************************************************************
        queryRepeatUser(user){//:
            for(let i=0;i<userDB.length;i++){
                if(userDB[i][0]==user) return true;
            }
            return false;//
        },
        addUser:function(user,password){// ()
            if(this.queryRepeatUser(user))return '';//
            userDB[userDB.length] = [user,password]//
            userCar.getCarDB()[user] = {};//
            return ''
        },
        examinePsw(user,password){// 
            let examine = this.queryRepeatUser(user);
            if(examine){//
                for(let i=0;i<userDB.length;i++){//
                    if(userDB[i][0]==user){
                        if(userDB[i][1] == password) return ['',0];
                        else return ['',1]; } }
            }else return '';
        },
        
        //**********************************************************************/
        getItems(name){//
            return goodsDB[name];
        },
        getItem(name,good){//
            return goodsDB[name][good];
        },
        getGoods(){//
            return goodsDB;
        },
        get14Goods(num){//{}N
            let count = 0;
            let temp1 = {};
            let temp2 = {};
            for(let key in goodsDB){
                for(let i in goodsDB[key]){
                    temp2[i] = goodsDB[key][i];//
                    count++;
                    if(count==num){
                        count=0;
                        break;
                    }
                }
                temp1[key]=temp2;//
                temp2={};
            }
            return temp1;
        },



    };//final return
}();

// 2.
export default myDB;