<template>
    <div class="board">
        <div class="login">
            <!--  -->
            <div class='login0' v-show='view0'>
                <h2></h2>
                <form>
                    <input type="text" placeholder='' v-model="userName">
                    <input type="password" placeholder='' v-model="password">
                </form>
                <div class="btnls">
                    <a class="btnl" @click='register()'></a>
                    <!-- LocalStorage  -->
                    <a class="btnl" @click='login()'></a>
                    <router-link to='/' class="btnl"></router-link>
                </div>
            </div>
            <!--  -->
            <div class='login0' v-show='view1'>
                <h2>{{ viewMsg }}</h2>
                <div class="btnls">
                    <router-link to='/login' class="btnl"
                        @click.native='back()'></router-link>
                    <router-link to='/' class="btnl"></router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data:function(){
            return{
                userName:"",
                password:"",
                view0:true,//
                view1:false,
                viewMsg:"",
            }
        },
        props:['DBfromApp'],//AppDB
        methods:{
            register:function(){
                if(this.nameCheck()){//DB=
                    let msg = this.DBfromApp.addUser(this.userName,this.password);
                    this.message(msg);
                    window.console.log('-: '+this.DBfromApp.getUserDB());
                }
            },
            login:function(){//
                if(this.nameCheck()){
                    let msg = this.DBfromApp.examinePsw(this.userName,this.password);
                    if(this.message(msg)){
                        //localStorage/sessionStorage
                        if(typeof(Storage)!=="undefined") localStorage.club1023User=this.userName;
                        else window.console.log('');
                    }
                }
            },
            nameCheck:function(){
                if(this.userName=="" || this.password==""){
                    this.message('  ');
                    return false;
                }
                else return true;
            },
            message:function(msg){//
                if(msg[1]==0){//
                    this.viewMsg=msg[0];
                    this.view1=true;
                    this.view0=false;
                    return true;
                }else if(msg[1]==1){//
                    this.viewMsg=msg[0];
                    this.view1=true;
                    this.view0=false;
                }else{
                    this.viewMsg=msg;
                    this.view1=true;
                    this.view0=false;
                }
            },
            back:function(){// 
                // window.console.log('er')
                this.view0=true;
                this.view1=false;
            }
        },
    }
</script>

<style scoped>
    @import '../assets/css/login/login.css';
</style>
